/* eslint-disable jsx-a11y/alt-text */
import { useState } from 'react';
import './App.css';

import BreakoutGame from './BreakoutGame_v3';
import BreakoutGame4 from './BreakoutGame_v4';
import { FinalWindow } from './FinalWindow';
import { StartWindow } from './StartWindow';
import { StartWindow2 } from './StartWindow2';

function App() {
  const [result, setResult] = useState(null)
  const [bricksCount, setBricksCount] = useState(null)
  const [bricks2Count, setBricks2Count] = useState(null)
  // const [gameStarted, setGameStarted] = useState(null)

  const handleRestart = () => {
    setResult(null)
    setBricksCount(null)
    setBricks2Count(null)
  }

  // return (
  //   <div className="App">
  //   <div className='border-bottom'></div>
  //   <div>
  //     <div className='app-frame' id="game-frame">
  //       <BreakoutGame />
  //     </div>
  //     <div className='border-bottom'></div>
  //   </div>
  //   </div>
  // )

  return (
    <div className="App">
      {!bricksCount && !bricks2Count &&
      <StartWindow selectNum={setBricksCount}/>
      }
      { bricksCount && !result && 
      (<>
      <div className='border-bottom'></div>
      <div>
        <div className='app-frame' id="game-frame">
          <BreakoutGame onFinish={setResult} n={bricksCount}/>
        </div>
        <div className='border-bottom'></div>
      </div>
      </>
      )
      }
      {!bricksCount && !bricks2Count &&
      <StartWindow2 selectNum={setBricks2Count}/>
      }
      { bricks2Count && !result && 
      (<>
      <div className='border-bottom'></div>
      <div>
        <div className='app-frame' id="game-frame">
          <BreakoutGame4 onFinish={setResult} n={bricks2Count}/>
        </div>
        <div className='border-bottom'></div>
      </div>
      </>
      )
      }
      {result && <FinalWindow txt={result} handleRestart={handleRestart}/> }
    </div>
  );
}

export default App;

